// src/app/control/page.tsx
"use client";

import { useEffect, useState } from "react";
import { CheckCircle2, Info, UserRound, RefreshCcw, Thermometer, Gauge, TimerReset, Timer } from "lucide-react";

type Mode = "MANU" | "AUTO";

export default function ControlPage() {
  // --- états simulés (à relier au PLC ensuite)
  const [faultOk, setFaultOk] = useState(true);
  const [autoReady, setAutoReady] = useState(false);
  const [mode, setMode] = useState<Mode>("MANU");
  const [dataRecordOn, setDataRecordOn] = useState(false);
  const [recordName, setRecordName] = useState("");
  const [airCooling, setAirCooling] = useState(true);

  // mini “valeurs instantanées” (F, TT, RS, ZP)
  const [F, setF]   = useState<number | null>(null);     // kN
  const [TT, setTT] = useState<number | null>(null);     // °C
  const [RS, setRS] = useState<number | null>(null);     // RPM
  const [ZP, setZP] = useState<number | null>(null);     // mm

  // horloge
  const [now, setNow] = useState(new Date());
  useEffect(() => {
    const id = setInterval(() => setNow(new Date()), 1000);
    return () => clearInterval(id);
  }, []);

  // TODO: branchement PLC —> remplace ce useEffect par tes fetch vers /api/read
  useEffect(() => {
    const id = setInterval(() => {
      setF(Math.random() * 12);
      setTT(20 + Math.random() * 60);
      setRS(Math.random() * 3000);
      setZP((Math.random() - 0.5) * 2);
    }, 500);
    return () => clearInterval(id);
  }, []);

  const StatCell = ({
    label,
    value,
    unit,
  }: {
    label: string;
    value: number | null;
    unit: string;
  }) => (
    <div className="flex items-center gap-2">
      <span className="text-xs text-gray-300 w-6">{label}</span>
      <input
        className="w-20 bg-gray-200/30 text-white/90 text-sm px-2 py-1 rounded outline-none"
        readOnly
        value={value === null ? "" : value.toFixed(1)}
      />
      <span className="text-xs text-gray-300 w-8">{unit}</span>
    </div>
  );

  return (
    <div className="min-h-screen w-full bg-[#ecf2f6]">
      {/* Top bar */}
      <div className="w-full bg-[#0f1c3a] text-white">
        <div className="mx-auto max-w-7xl px-6 py-3 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="text-lg font-semibold">stirweld</div>
            <div className="text-xs opacity-70">V4 v2.0</div>
          </div>

          <div className="flex items-center gap-6">
            <div className="hidden md:flex items-center gap-4">
              <StatCell label="F"  value={F}  unit="kN" />
              <StatCell label="TT" value={TT} unit="°C" />
              <StatCell label="RS" value={RS} unit="RPM" />
              <StatCell label="ZP" value={ZP} unit="mm" />
            </div>

            <button
              className="rounded-full bg-white/10 p-2 hover:bg-white/20 transition"
              title="Refresh"
            >
              <RefreshCcw className="w-5 h-5" />
            </button>

            <div className="text-right leading-tight">
              <div className="text-sm font-semibold">
                {now.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit", second: "2-digit" })}
              </div>
              <div className="text-xs opacity-70">
                {now.toLocaleDateString()}
              </div>
            </div>

            <div className="flex items-center gap-2">
              <UserRound className="w-6 h-6 opacity-90" />
              <span className="text-sm opacity-90">__SystemUser</span>
            </div>
          </div>
        </div>
      </div>

      {/* Contenu */}
      <div className="mx-auto max-w-7xl px-6 py-10">
        <div className="text-2xl font-extrabold tracking-wide text-[#0f1c3a] mb-4">START CONDITIONS</div>

        <div className="bg-white rounded-2xl shadow border border-gray-200">
          {/* Bloc conditions */}
          <div className="p-6 md:p-8">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 items-center">
              <div className="space-y-6">
                {/* Fault */}
                <div className="flex items-center gap-4">
                  <button
                    onClick={() => setFaultOk(v => !v)}
                    className={`w-10 h-10 rounded-full border flex items-center justify-center
                    ${faultOk ? "bg-emerald-500 text-white border-emerald-600" : "bg-gray-200 border-gray-300 text-gray-500"}`}
                    title="Fault state"
                  >
                    <CheckCircle2 className="w-6 h-6" />
                  </button>
                  <span className="text-xl font-semibold text-[#0f1c3a]">Fault</span>
                </div>

                {/* Auto ready */}
                <div className="flex items-center gap-4">
                  <button
                    onClick={() => setAutoReady(v => !v)}
                    className={`w-10 h-10 rounded-full border flex items-center justify-center
                    ${autoReady ? "bg-emerald-400 text-white border-emerald-500" : "bg-gray-200 border-gray-300 text-gray-500"}`}
                    title="Auto readiness"
                  >
                    <Info className="w-5 h-5" />
                  </button>
                  <span className="text-xl font-semibold text-[#0f1c3a]">Auto</span>
                </div>

                <div className="text-[#0f1c3a]/80 font-semibold">
                  Need Initiales Conditions to switch Auto mode
                </div>
              </div>

              {/* Mode MANU/AUTO grand rond + texte */}
              <div className="flex items-center justify-center gap-6">
                <button
                  onClick={() => setMode(prev => (prev === "MANU" ? "AUTO" : "MANU"))}
                  className={`w-24 h-24 rounded-full border-2 transition 
                    ${mode === "AUTO" ? "bg-emerald-500 border-emerald-600" : "bg-gray-100 border-gray-300"}`}
                  title="Toggle mode"
                />
                <div className={`text-6xl font-extrabold ${mode === "AUTO" ? "text-emerald-600" : "text-[#0f1c3a]"}`}>
                  {mode}
                </div>
              </div>
            </div>
          </div>

          {/* Bandeau Data Record */}
          <div className="bg-gray-200/60 border-t border-gray-300 p-4 md:p-6 rounded-b-2xl">
            <div className="flex flex-col md:flex-row md:items-center gap-4">
              <button
                onClick={() => setDataRecordOn(v => !v)}
                className={`px-4 py-2 rounded-full font-semibold shadow 
                  ${dataRecordOn ? "bg-sky-500 text-white" : "bg-gray-300 text-gray-700"}`}
              >
                Data Record
              </button>

              <div className="flex-1">
                <label className="block text-sm font-semibold text-[#0f1c3a] mb-1">
                  Write the name of this recording file here
                </label>
                <input
                  className="w-full bg-white border border-gray-300 rounded-lg px-3 py-2 outline-none focus:ring-2 focus:ring-sky-400"
                  value={recordName}
                  onChange={(e) => setRecordName(e.target.value)}
                  placeholder="MyRecording_001"
                />
              </div>
            </div>
          </div>
        </div>

        {/* Footer action */}
        <div className="mt-6 flex items-center justify-end gap-3">
          <button
            onClick={() => setAirCooling(v => !v)}
            className={`flex items-center gap-3 px-5 py-3 rounded-full font-semibold shadow
              ${airCooling ? "bg-[#0f1c3a] text-white" : "bg-gray-300 text-gray-700"}`}
          >
            <CheckCircle2 className="w-5 h-5" />
            Air Cooling
          </button>
        </div>
      </div>
    </div>
  );
}
